import { Component, OnInit, ViewChild } from '@angular/core';
import { Router,NavigationExtras } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, App_loginApi, BooksApi } from '../../../../shared/loopback-sdk';
import { ActivatedRoute } from "@angular/router";
import * as moment from 'moment'; //for date manupalation
import {ConfirmationService} from 'primeng/primeng';
import { Lightbox } from 'angular2-lightbox';
import {PaginatorModule} from 'primeng/primeng';
import {Message,MenuItem,SelectItem} from 'primeng/primeng';
import { CookieService } from 'angular2-cookie/core';
declare var window:any;
declare var $;
declare var sdkPrefix;
@Component({
	selector: "viewBook",
	templateUrl: 'view-book.component.html',
	providers: [ConfirmationService],
})

export class ViewBookComponent {
	result: any;
	success: boolean;
	display: boolean;
	public id: number;
	public role: string;
	submited:any=false;

	bookData: any = [];
	backLink:any = "";
	busy:any;
	emptyMessage:String="No Result Found";
	displayConfirm: boolean = false;
	msgs: Message[] = [];
	maxDate:Date;
	loading:boolean = true;
	chapterName:any;
	chapterStatus:any="";
	createdAt:any;
	chapterNameSearch:any;
	chapterStatusSearch:any="";
	createdAtSearch:any;
	paginationVal:any=false;
	perPageLimit:number = 10;
	offset:number=0;
	total:number;
	_albums:any = [];
	chapterData:any=[];
	ifEmpty:boolean=false;
	constructor(private router:Router, private bookApi:BooksApi, private _cookieService: CookieService, private _lightbox: Lightbox, private confirmationService: ConfirmationService, private appLoginApi:App_loginApi,private loopAuth:LoopBackAuth,private route: ActivatedRoute) {
		this.id = this.route.snapshot.params['id'];
		this.maxDate = new Date();
	}

	ngOnInit(): void {
		this.loading = true;
		let dataObj={};
		let uid=this.id;
		let dataArrObj={
			"id":this.id
		}
		let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		this.bookApi.getBooks(dataArrObj).subscribe(
			data=>{
				this.loading = false;
				if(data.result.length>0){
					if(data.result[0].status == 1){
						data.result[0].status = 'Active';
					}else{
						data.result[0].status = 'Inactive';
					}
					if(data.result[0].created_at){
						data.result[0].created_at = moment(data.result[0].created_at).format("DD - MMM - YYYY");
					}
					if(data.result[0].logo_image){
						data.result[0].logo_image = data.result[0].logo_image+'?access_token='+loginFlagToken;
						data.result[0].profileArr = [{ src:data.result[0].logo_image, thumb: data.result[0].logo_image}];
					}
					this.bookData = data.result[0];
					this.getCount();
				}
			},
			err=>{
				this.loading = false;
			},
			()=>{

			}
		);
	}

	getCount(){
		this.loading = true;
		if(this.createdAt){
			this.createdAtSearch = moment(this.createdAt).format("YYYY-MM-DD hh:mm:ss.SSS");
		}
		var dataArrObj = {
			"book_id":this.id,
			"name":this.chapterNameSearch,
			"created_at":this.createdAtSearch,
			"status":this.chapterStatusSearch,
			"page":this.offset,
			"limit":this.perPageLimit
		}
		this.bookApi.getChaptersCount(dataArrObj).subscribe(
			data=>{
				this.total = data['result'][0]['total'];
				if(this.total <= 10){
					this.paginationVal = false;
				}else{
					this.paginationVal = true;
				}
				this.getData(dataArrObj);
			},
			err=>{
				this.loading = false;
			},
			()=>{

			}
		)
	}

	getData(dataArrObj){
		this.loading = true;
		let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		this._albums = [];
		this.bookApi.getChapters(dataArrObj).subscribe(
			data=>{
				this.loading = false;
				var totalData = data.result.length;
				this.ifEmpty = false;
				if(totalData == 0){
					this.ifEmpty = true;
				}
				for(var i=0; i<totalData; i++){
					if(data.result[i].status == 1){
						data.result[i].status = 'Active';
					}else{
						data.result[i].status = 'Inactive';
					}
					data.result[i].created_at = moment(data.result[i].createdAt).format("DD - MMM - YYYY");
				}
				this.chapterData = data.result;

				console.log("chapterData", this.chapterData);
			},
			err=>{
				this.loading = false;
			},
			()=>{}
		)
	}

	activeInactiveClick(){
		this.confirmationService.confirm({
			message: 'Are you sure that you want to proceed?',
			header: 'Confirmation',
			icon: 'fa fa-question-circle',
			accept: () => {
			  // this.msgs = [{severity:'info', summary:'Confirmed', detail:'You have accepted'}];
			  this.activeInactiveSubmit();
			},
			reject: () => {
			 // this.msgs = [{severity:'info', summary:'Rejected', detail:'You have rejected'}];
			}
		});
	}

	activeInactiveSubmit(){
		this.loading = true;
		let dataObj={};
		if(this.bookData['status']=='Active'){
			dataObj['status']=0;
		}else{
			dataObj['status']=1;
		}
		let bookId=this.id;
		this.msgs.length=0;
		this.bookApi.addEditBooks(dataObj,bookId).subscribe((respData:any)=>{
			this.loading = false;
			this.msgs.push({severity:'success', summary:'Success Message', detail:'Book Status Has Been Changed Successfully.'});
			setTimeout(()=>{
				window.location.reload();
			},3000);
		},(error)=>{
			//console.log('respData error',error);
			this.msgs.push({severity:'warn', summary:'Error Message', detail:'Somthing Went Wrong. Please try again later.'});
		});
	}

	paginate(event) {
		this.chapterData = [];
		this.offset=event.page;
		this.paginationVal=true;
		if(this.createdAt){
			this.createdAtSearch = moment(this.createdAt).format("YYYY-MM-DD hh:mm:ss.SSS");
		}
		var dataArrObj = {
			"book_id":this.id,
			"name":this.chapterNameSearch,
			"created_at":this.createdAtSearch,
			"status":this.chapterStatusSearch,
			"page":this.offset,
			"limit":this.perPageLimit
		}
		this.getData(dataArrObj);
	}


	searchData(){
		this.offset = 0;
		this.paginationVal=false;
		this.ifEmpty = false;
		this.chapterData =[];
		this.chapterNameSearch = this.chapterName;
		this.createdAtSearch = this.createdAt;
		this.chapterStatusSearch = this.chapterStatus;
		this.getCount();
	}

	resetPage(){
		this.offset = 0;
		this.paginationVal=false;
		this.chapterName = "";
		this.createdAt = "";
		this.createdAtSearch = "";
		this.chapterStatusSearch = "";
		this.chapterNameSearch = "";
		this.getCount();
	}

	open(data,index: number): void {
		// override the default config
		this._lightbox.open(data, index, { wrapAround: true, showImageNumberLabel: true });
	}
}


